@import url(https://fonts.googleapis.com/css?family=Roboto:300,400);
.App {
  text-align: center;
  width: 100%;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #09d3ac;
}
body {
  position: relative;
}
html, body, #root, .App {
  height:100%;
  margin: 0;
}
h1, h2, h3, h4, h5, h6, p, a {
  font-family: 'Roboto', sans-serif;
  margin: 0;
}
.homeback {
  text-decoration: none;
  color: white;
  position: absolute;
  font-size: 1.4em;
  top: 20px;
  left: 20px;
}
.loading {
  width: 100%;
  height: 100%;
  position: relative;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-color: #2a2a72;
  background-image: linear-gradient(315deg, #2a2a72 0%, #009ffd 74%);
}
.loading h2 {
  color: white;
  font-weight: 300;
  font-size: 2em;
}
.clear-day {
  background-color: #5abdff;
}
.clear-night {
  background-color: #0e1233;
}
.partly-cloudy-day  {
  background: url(/weather-app/static/media/partly-cloud.dd34329b.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.partly-cloudy-night {
  background: url(/weather-app/static/media/partly-cloud-night.0d13dc3f.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.cloudy {
  background: url(/weather-app/static/media/cloudy.516e2a69.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.rain {
  background: url(/weather-app/static/media/rain.4f860d4f.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.snow {
  background: url(/weather-app/static/media/snow.863eb85d.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.sleet {
  background: url(/weather-app/static/media/sleet.390f302e.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.wind {
  background: url(/weather-app/static/media/wind.d0c627f8.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.fog {
  background: url(/weather-app/static/media/fog.de1aa394.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.hail {
  background: url(/weather-app/static/media/hail.48d5f34a.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.thunderstorm {
  background: url(/weather-app/static/media/thunderstorm.181d8cad.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}
.tornado {
  background: url(/weather-app/static/media/tornado.af13196d.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}
.restaurantbkgd {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#48c6ef), to(#6f86d6));
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
}
.wrapper {
  width: 100%;
  /* height: 100%; */
  position: relative;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-repeat: no-repeat;
    background-size: cover;
  background-position: center center;
  padding: 40px 0;
}
.wrapper2 {
  width: 100%;
  height: 100%;
  position: relative;
  display:flex;
  flex-direction:column;
  align-items:center;
  background-repeat: no-repeat;
    background-size: cover;
  background-position: center center;
  padding: 40px 0;
}
.container {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 85%;
  min-width: 300px;
  background-color: rgba(255, 255, 255, .15);  
  -webkit-backdrop-filter: blur(5px);  
          backdrop-filter: blur(5px);
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  /*padding: 10px 20px;*/
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
  margin-bottom: 15px;
}
.container2 {
  position: relative;
  display: block;
  margin: 40px auto;
  width: 85%;
  min-width: 300px;
  background-color: rgba(255, 255, 255, .15);  
  /* backdrop-filter: blur(5px); */
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  /*padding: 10px 20px;*/
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
}
.cuisine-suggestion {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 85%;
  min-width: 300px;
  color: white;
  background-color: rgba(255, 255, 255, .15);  
  -webkit-backdrop-filter: blur(5px);  
          backdrop-filter: blur(5px);
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  padding: 10px 0 20px;
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
}
.cuisine-suggestion .cuisine-text {
  padding: 20px 0;
}
.header h1 {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  margin: 0;
  color: #ffffff;
  padding: 20px 0;
}
.weatherbox {
  text-align: center;
  padding: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.temperaturebox, .forecastbox {
  width: 49%;
}
.temperaturebox {
  border-right: 1px solid #ffffff;
}
.forecastbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  padding: 0 20px;
  box-sizing: border-box;
}
.locationbox {
  position: relative;
  display: flex;
  justify-content:         center;
  flex-direction: column;
  /* padding: 20px 20px 0; */
}
.datebox h3 {
  font-weight: 300;
  margin-top: 0;
  color: white;
}
.locactioninfobox {
  /* -webkit-flex-basis: 350px;
  flex-basis:         350px;*/
  /* text-align: left; */
}
.cityarea h2 {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: 2em;
  margin: 0;
}
.countryarea {
  position: relative;
  display: flex;
  justify-content:         center;
  margin: 0 auto;
  /* text-align: left; */
}
.countryarea h3 {
  margin: 0 0 5px 0;
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  color: #ffffff;
}
.countryarea h3:first-child {
  margin-right: 8px;
}
.mainline hr {
  border: 0;
  height: 1px;
  background: #ffffff;
  opacity: 0.2;
}
.temperaturearea {
  padding-bottom: 10px;
}
.toptempbox {
  position: relative;
  display: flex;
  justify-content:         center;
}
.tempbox {
  /* -webkit-flex-basis: 180px;
  flex-basis:         180px; */
}
.temperaturearea span#temp {
  position: relative;
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-size: 10em;
}
.temperaturearea #temp:after {
    content: '';
    position: absolute;
    height: 10px;
    width: 10px;
    top: 16px;
    right: -17px;
    border: 3px solid #ffffff;
    border-radius: 50%;
}
.daycard {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}
.fdetails {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fdetails .wi {
  margin-right: 10px;
}
.daycard #temp {
  position: relative;
  margin-right: 6px; 
}
.daycard h2 {
  font-weight: 300;
}
.daycard #temp:after {
  content: '';
  position: absolute;
  height: 3px;
  width: 3px;
  top: 3px;
  right: -7px;
  border: 2px solid #ffffff;
  border-radius: 50%;
}
.wconditionsbox {
  align-self: flex-end;
  padding-bottom: 35px;
  padding-left: 5px;
   /* -webkit-flex-basis: 100px;
  flex-basis:         100px; */
}
.wconditionsbox span {
  display: block;
  /* text-align: left; */
}
#wparameter {
  font-size: 3.5em;
}
#wdescription {
  font-size: 1em;
}
#wparameter, #wdescription {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
}
.weather > span {
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  color: #ffffff;
}
.weather > span:before {
    content: '';
    position: absolute;
    left: -10px;
    top: 0px;
    height: 3px;
    width: 3px;
    border: 2px solid #ffffff;
    border-radius: 50%;
}
.main-toggle span {
  margin: 0 0 0 16px;
}
.main-toggle span:last-child {
  margin-left: 11px;
}
.weather button {
		background: #6bbf6b;
		border: none;
		border-radius: 30px;
		outline: none;
		width: 45px;
		height: 20px;
		margin: 5px 5px 0;
		cursor: pointer;
		position: relative;
		-webkit-transition: background .2s;
		transition: background .2s;
}
.weather button:active {
		background: #67b567;
	}
.weather #unit-toggle {
		position: absolute;
		display: inline-block;
		left: -8px;
		top: 2px;
		width: 15px;
		height: 15px;
		background: #fff;
		border-radius: 50%;
		-webkit-transition: left .2s;
		transition: left .2s;
}
#unit-toggle.toggle {
		left: 16px;
}
.cuisine-suggestion h3 span {
  color: black;
  background-color: rgba(255, 255, 255, .45);
  padding: 5px 8px;
  border-radius: 4px;
}
.restaurant-box {
  width: 90%;
  margin: 0 auto;
}
.restaurant-box a {
  text-decoration: none;
  color: white;
}
.card {
  display: flex;
  position: relative;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
  border: 1px solid rgba(255, 255, 255, .45);
}
.card .restaurantimg {
  width: 80px;
  height: 80px;
  margin-right: 10px;
}
.restaurantimg img {
  width: 100%;
  height: auto;
}
.restaurant-details, .single-restaurant-details {
  text-align: left;
}
.restaurant-details h2, .restaurant-details h3, .single-restaurant-details h2, .single-restaurant-details h3 {
  padding-bottom: 2px;
}
.restaurant-details h2 {
  font-size: 1.2em;
}
.restaurant-details span.ratingtext, .single-restaurant-details span.ratingtext {
  font-size: 0.5em;
}
.restaurant-details h3 {
  font-size: 1em;
  font-weight: 300;
}
.restaurant-details h4 {
  font-size: 0.8em;
  font-weight: 300;
}
.star {
  font-size: 1.2em;
}
.stars-outer {
  display: inline-block;
  position: relative;
  font-family: FontAwesome;
}
.stars-outer::before {
  content: "\f006 \f006 \f006 \f006 \f006";
}
.stars-inner {
  position: absolute;
  top: 0;
  left: 0;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}
.stars-inner::before {
  content: "\f005 \f005 \f005 \f005 \f005";
  /* color: #f8ce0b; */
}
.radius-text {
  margin: 0 0 10px;
  font-weight: 300;
  text-align: left;
}
.restaurantbox {
  width: 100%;
  padding: 20px;
  display: flex;
  box-sizing: border-box;
}
.restaurantbox .restaurantimg {
  width: 200px;
  height: 200px;
  margin-right: 20px;
}
.restaurantbox .single-restaurant-details h2 {
  font-size: 2em;
  margin-bottom: 10px;
  color: white;
}
.restaurantbox .single-restaurant-details h3 {
  font-weight: 300;
  margin-bottom: 10px;
  color: white;
}
.raddress {
  font-size: 1.5em;
}
.restaurantbox .single-restaurant-details h4 {
  font-size: 1.2em;
  font-weight: 300;
  color: white;
}
.single-restaurant-details {
  color: white;
}
.restaurant-labels, .rlinks {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 25px 0;
  flex-wrap: wrap;
}
.rlinks {
  padding-top: 0px;
}
.rbox {
  width: 49%;
  margin-bottom: 20px;
  color: white;
}
.rphone a {
  text-decoration: none;
  font-size: 1em;
  color: white;
}
.rprice h3 span {
  font-size: 1em;
}
.zomatobtn a {
  display: inline-block;
  padding: 10px 12px;
  font-size: 1em;
  color: black;
  background-color: white;
  text-decoration: none;
  border-radius: 5px;
  -webkit-transition: 1s all;
  transition: 1s all;
}
.zomatobtn a:hover {
  background-color: lightgray;
}
.directionbtn button {
  display: inline-block;
  padding: 10px 12px;
  font-size: 1em;
  color: black;
  background-color: white;
  border-radius: 5px;
  border: none;
  -webkit-transition: 1s all;
  transition: 1s all;
  cursor: pointer;
}
.directionbtn button:hover {
  background-color: lightgray;
}
@media screen and (max-width: 479px) {
  .weatherbox {
    flex-direction: column;
  }
  .temperaturebox, .forecastbox {
    width: 100%;
}
  .temperaturebox {
    border-right: none;
    margin-bottom: 30px;
  }
  .forecastbox {
    justify-content: flex-start;
    padding-left: 0px;
  }
   .container, .cuisine-suggestion {
  width: 90%;
  min-width: 300px;
}
.restaurant-details h2 {
  font-size: 0.8em;
}
.restaurant-details h3 {
  font-size: 0.6em;
}
.card .restaurantimg {
  width: 70px;
  height: 70px;
}
.restaurant-details span.ratingtext, .restaurant-details h4 {
  display: none;
}
}
@media screen and (min-width: 480px) and (max-width: 900px) {
  .container, .cuisine-suggestion {
  width: 65%;
  min-width: 300px;
  }
}
